import SupButton from './components/supButton';
import SubButton from './components/subButton';
import GroupButton from './components/groupButton';

export default function toolbar(vertAlignConfig) {
  const vertAlignLocaleConfig = vertAlignConfig.locale || {};
  return {
    buttons: {
      default: GroupButton,
      sup: SupButton,
      sub: SubButton,
      // For backward-compatibility
      supMark: SupButton,
      subMark: SubButton,
    },
    locale: vertAlignLocaleConfig,
  };
}
